import { CommandInteractionOptionResolver } from "discord.js";
import { client } from "../startBot";
import { Event } from "../extensions/Event";
import { ExtendedInteraction } from "../constants/Command";
import Log from "../services/logs";
import RegisterModule from "../handlers/register";
import chalk from "chalk";
export default new Event("interactionCreate", async (interaction) => {
  const timeStr = `${new Date().toLocaleString()}`;

  if (interaction.isCommand()) {
    const command = client.commands.get(interaction.commandName);
    if (!command)
      return interaction.followUp("`‚ùå` The command doesn't exist!");

    command.run({
      args: interaction.options as CommandInteractionOptionResolver,
      client,
      interaction: interaction as ExtendedInteraction,
    });
    Log.customLog(
      `${interaction.user.tag} used ${interaction.commandName} at ${chalk.bold(
        chalk.greenBright(timeStr)
      )}`,
      "DiscordSocket:ApplicationCommand"
    );
  } else if (interaction.isMessageComponent()) {
    Log.customLog(
      `${interaction.user.tag} used ${interaction.customId} at ${chalk.bold(
        chalk.greenBright(timeStr)
      )}`,
      "DiscordSocket:MessageComponent"
    );
  } else if (interaction.isModalSubmit()) {
    Log.customLog(
      `${interaction.user.tag} used ${interaction.customId} at ${chalk.bold(
        chalk.greenBright(timeStr)
      )}`,
      "DiscordSocket:ModalSubmit"
    );

    if (interaction.customId === "register") {
      RegisterModule.onModalSubmit(interaction);
    }
  }
});
